.main{
    min-height: 93vh;
    display: flex;
    align-items: center;
    background-image: url('./bg-1.jpg');
    background-position: center;
    background-size: cover;
}

.title{
    font-size: 95px;
}

.subtitle{
    font-size: 25px;
    padding-top: 10px;
}

.intro__text{
    width: 100%;
    text-align: center;
    margin-left: 28%;
}
.button__cont > a {
    text-decoration: none;
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
}

.btn_l{
    width: 200px;
    height: 55px;
}

@media only screen and (min-width: 1200px){
    .button__cont{
        padding: 0 200px;
    }
    .title{
        padding: 0 100px;
    }
}

@media only screen and (max-width: 768px){
    .btn_l{
        width: 150px;
        height: 50px ;
    }
    .title{
        font-size: 60px;
    }
}